*{
    box-sizing:border-box;
}
body{
    font-family: sans-serif;
    margin: 20px;
    padding: 0px;
}
h1{
    margin-top: 0;
    font-size: 22px;
}

h2{
    margin-top: 0;
    font-size: 20px;
}
h3{
    margin-top: 0px;
    font-size: 18px;
}
h5{
    margin-top: 0px;
    font-size: 14px;
}
h6{
    margin-top: 0px;
    font-size: 12px;
}


code {
    font-size: 1.2em;
  }
  
  ul {
    padding-inline-start: 20px;
  }
  
  * {
    box-sizing: border-box;
  }
  
  body {
    font-family: sans-serif;
    margin: 20px;
    padding: 0;
  }
  
  .square {
    background: #fff;
    border: 1px solid #999;
    float: left;
    font-size: 24px;
    font-weight: bold;
    line-height: 34px;
    height: 34px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 34px;
  }
  
  .board-row:after {
    clear: both;
    content: '';
    display: table;
  }
  
  .status {
    margin-bottom: 10px;
  }
  .game {
    display: flex;
    flex-direction: row;
  }
  
  .game-info {
    margin-left: 20px;
  }
  

